.photo-faq-img {
    width: 7.125rem;
}

.section-faq {
    padding: 3rem 0 3rem;

    @include media-breakpoint-up(md) {
        padding: 6rem 0 3rem;
    }

    @include media-breakpoint-up(lg) {
        padding: 7rem 0;
    }

    .container {
        @include media-breakpoint-up(lg) {
            max-width: 63.375rem;
        }
    }

    .accordion-item {
        margin-bottom: 1px;
    }
}
