.navbar {
    position: sticky;
    top: 0;
    padding: .75rem 0;
    background: #fff;
    border-bottom: 1px solid #b2b2b2;
    z-index: 1001;

    @include media-breakpoint-up(lg) {
        position: relative;
        padding: 2rem 0;
    }

    .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

.navbar__body {
    display: flex;
    align-items: center;

    .newsletter {
        margin-top: 2rem;
    }

    .social {
        display: flex;
        width: 100%;
        justify-content: center;
        margin-top: 2rem;

        @include media-breakpoint-up(lg) {
            display: none;
        }
    }

    .newsletter {
        max-width: 22.5rem;
        margin-left: auto;
        margin-right: auto;
    }

}

.navbar__action {
    display: flex;
    align-items: center;
    margin-left: 2.5rem;
    gap: 1.75rem;
}

.nav-overlay {
    display: flex;
    flex-direction: column;
    align-items: center;
}