.promo {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 1rem;
    border: 1px solid $light;

    @include media-breakpoint-up(md) {
        margin-top: 2.25rem;
    }

    @include media-breakpoint-up(lg) {
        min-height: 37.5rem;
        padding: 6.875rem 5.25rem;
    }

    .amount {
        margin-top: 1rem;

        @include media-breakpoint-up(md) {
            margin-top: 1.75rem;
        }

        + .button {
            margin-top: 1.75rem;
        }
    }

    .amount__current {
        font-size: 1.6rem;

        @include media-breakpoint-up(sm) {
            font-size: 1.75rem;
        }
        
        @include media-breakpoint-up(md) {
            margin-right: 1rem;
            font-size: 2.25rem;
        }
    }

    .amount__old {
        font-size: 1.125rem;
        @include media-breakpoint-up(md) {
            font-size: 1.625rem;
        }
    }
}

.promo__body {
    padding: 1.5rem 1rem 0;
    max-width: 32rem;
    margin: 0 auto;
    text-align: center;

    @include media-breakpoint-up(sm) {
        padding: 3rem 2rem 0;
    }

    @include media-breakpoint-up(lg) {
        padding: 0;
        margin: 0;
        max-width: 25.5rem;
        text-align: left;
    }
}

.promo__title {
    display: block;
    font-family: $headings-font-family;
    font-size: 1.375rem;
    font-weight: $fw-bold;
    text-decoration: none;
    color: #000;
    line-height: 1.25;

    @include media-breakpoint-up(sm) {
        font-size: 2.125rem;
    }
}

.promo__desc {
    display: none;
    
    @include media-breakpoint-up(md) {
        display: block;
        margin-top: 1.5rem;
        font-size: 0.9375rem;
        font-weight: $fw-light;
    }
}

.promo__thumb {
    text-align: right;

    @include media-breakpoint-up(lg) {
        position: absolute;
        bottom: 0;
        right: 0;
    }
}
