.card-row {
    position: relative;
    display: flex;
    align-items: center;

    .quantity {
        @include media-breakpoint-down(md) {
            gap: .5rem;
        }
    }

    .quantity__btn {
        width: 2rem;
        height: 2rem;
    }

    .quantity__field {
        width: 2.35rem;
        height: 2.35rem;
    }
}

.card-row__thumb {
    position: relative;
    width: 40%;
    padding-right: 1rem;

    @include media-breakpoint-up(md) {
        width: 17.5rem;
    }

    @include media-breakpoint-up(lg) {
        padding: 0 1.25rem;
    }

    .label {
        position: absolute;
        top: .5rem;
        left: 0;

        @include media-breakpoint-up(sm) {
            top: 1.5rem;
        }

        @include media-breakpoint-up(md) {
            top: 2.5rem;
            left: 1.125rem;
        }
    }
}

.card-row__body {
    width: 100%;

    @include media-breakpoint-up(md) {
        width: 26rem;
    }

    @include media-breakpoint-up(lg) {
        padding-right: 2.5rem;
    }

    .card__title {
        @include media-breakpoint-down(md) {
            font-size: 1rem;
        }
    }
}

.card-row__action {
    width: 10.5rem;
}

.card-row__cancel {
    position: absolute;
    bottom: 1rem;
    right: 0;

    @include media-breakpoint-up(lg) {
        position: static;
        margin-left: auto;
    }
}

.card-row__wrap {
    display: flex;
    width: 60%;
    flex-direction: column;
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;
    gap: 1.25rem;

    @include media-breakpoint-up(md) {
        width: calc(100% - 17.5rem);
        padding-right: 2.5rem;
    }

    @include media-breakpoint-up(lg) {
        flex-direction: row;
        align-items: center;
        gap: 0;
    }
}