.section-opinions {
    position: relative;
    z-index: 1;
}

.section-opinions-bg {
    display: none;

    @include media-breakpoint-up(lg) {
        position: absolute;
        top: -2.875rem;
        left: 0;
        display: block;
        width: 100%;
        height: 24.4375rem;
        z-index: -1;
        text-align: right;
    }
}

.opinion {
    position: relative;
    padding-left: 3.125rem;
}

.opinion-thumb {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 2.75rem;
}

.opinion-body {
    padding: .75rem;
    border-radius: 1.125rem;
    background: #f1f1f1;
    color: #0e0e0e;
    line-height: 1.3;
}

.swiper-opinions {
    .swiper-slide {
        width: 19rem;

        @include media-breakpoint-up(sm) {
            width: 21.25rem;
        }
    }
    .swiper-pagination {
        position: static;
    }
    .swiper-pagination-bullet {
        width: .75rem;
        height: .75rem;
        background: #eff0f1;
        opacity: 1;

        @include media-breakpoint-up(md) {
            width: 1.1875rem;
            height: 1.1875rem;
        }

        &.swiper-pagination-bullet-active {
            background: $primary;
        }
    }
}

.section-opinions-img {
    width: 48rem;
}
