.form {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;    
    margin-top: 2rem;
    @include media-breakpoint-up(md) {
        margin-top: 2.85rem;
        padding: 0 0 0 1.125rem;
    }

    .row {
        gap: 1.5rem 0;
    }
}

.form-block {
    display: block;
}

.form-row {
    position: relative;
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    gap: 0 1.5rem;

    @include media-breakpoint-up(md) {
        gap: 0 5rem;
    }
}

.form-check {
    position: relative;
}

.form-check-logo {
    max-width: 5.25rem;
    opacity: .2;
    transition: opacity .25s ease-in-out;

    @include media-breakpoint-up(md) {
        max-width: 100%;
    }
}

.form-check-input {
    position: absolute;
    clip: rect(0,0,0,0);
    pointer-events: none;

    &:checked {
        + .form-check-label {
            &:after {
                transform: scale(.45);
            }

            .form-check-logo {
                opacity: 1;
            }
        }
    }

    &:focus {
        + .form-check-label {
            &:before {
                border-color: #acacac;
            }
        }
    }

    &[type="radio"] {
        + .form-check-label {
            &:before,
            &:after {
                border-radius: 50%;
            }
        }
    }
}

.form-check-label {
    position: relative;
    padding: 0 0 0 2.5rem;
    font-size: 1rem;
    font-weight: $fw-semibold;
    cursor: pointer;

    &:hover {
        &:before {
            border-color: #acacac;
        }
    }

    @include media-breakpoint-up(md) {
        padding: 0 0 0 3.25rem;
        font-size: 1.0625rem;
    }

    &:before,
    &:after {
        content: "";
        position: absolute;
        top: -.3rem;
        left: 0;
        width: 1.75rem;
        height: 1.75rem;
        border: 1px solid #e3e3e3;

        @include media-breakpoint-up(md) {
            top: -.15rem;
        }
    }

    &:before {
        transition: border-color .25s ease-in-out;
    }

    &:after {
        border: 0;
        background: #000;
        transform: scale(0);
        transition: transform .25s ease-in-out;
    }

    strong {
        font-family: $font-family-condensed;
        font-weight: $fw-bold;
    }
}

.form-payments {
    display: flex;
    padding: 1.5rem 0;
    max-width: 25rem;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    gap: 1rem;

    @include media-breakpoint-up(md) {
        padding: 3rem 0;
        max-width: 100%;
    }
}

.form-control {
    width: 100%;
    height: 3.5rem;
    padding: 0 1.25rem;
    background: #fff;
    border: 1px solid #ebebeb;
    font-size: 1.0625rem;
    transition: border-color .25s ease-in-out;

    &:focus {
        outline: 0;
        border-color: #acacac;
    }

    &.has-error {
        border-color: $primary;
    }
}

select {
    &.form-control {
        padding-right: 3.125rem;
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
        background-repeat: no-repeat;
        background-position: right .75rem center;
        background-size: 24px 18px;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
    }
}

.form-textarea {
    @extend .form-control;
    height: auto;
    padding-top: 1rem;
    padding-bottom: 1rem;

    &::placeholder {
        font-weight: 700;
        font-size: 1rem;
    }
}

.form-label {
    margin-bottom: .5rem;
    font-size: 1.0625rem;
    
    &:not(.form-label-static) {
        position: absolute;
        top: -.85rem;
        left: 0;
        display: inline-block;
        margin: 0 1.25rem;
        background: #fff;
        opacity: 0;
        pointer-events: none;
        transition: opacity .25s, transform .25s;
        transform: translateY(1rem);
        z-index: 2;
        &.form-label-show {
            transform: translateY(0);
            opacity: 1;
            pointer-events: auto;
        }
    }
}

.col-4 {
    select.form-control {
        @include media-breakpoint-down(sm) {
            background: #fff;
            padding-right: 1.25rem;
        }
    }
}