.button-icon {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    background: none;
    border: none;
    outline: 0;
    color: #000;
    line-height: 1;
    font-size: 0.9375rem;
    font-weight: $fw-semibold;
    text-transform: uppercase;
    text-decoration: none;

    &.active {
        color: $primary;
    }

    .count {
        position: absolute;
        bottom: -.5rem;
        right: -.5rem;
        z-index: 2;
    }
}

.button-icon_nav {
    z-index: 1000;
    &:focus {
        color: #000;
    }

    &.active {
        color: $primary;
    }
    
    @include media-breakpoint-up(lg) {
        display: none;
    }
}