.label {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: .45rem .5rem;
    background: #000;
    font-size: 0.5rem;
    font-weight: $fw-medium;
    text-transform: uppercase;
    letter-spacing: 2px;
    text-align: center;
    color: #fff;

    @include media-breakpoint-up(md) {
        font-size: 0.6875rem;
    }
}