.nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    list-style: none;
    margin: 0;

    @include media-breakpoint-up(lg) {
        flex-direction: row;
    }
}

.nav_main {
    padding-left: 0;

    .link {
        display: block;
        padding: .5rem 1.0625rem;
        font-family: $headings-font-family;
        font-weight: $fw-semibold;

        @include media-breakpoint-down(lg) {
            font-size: 1.375rem;
        }
    }
}
