.contact {
    text-align: center;

    .heading {
        margin-top: 1.125rem;

        + .paragraph {
            margin-top: .875rem;
        }
    }
}

.contact__mail {
    display: inline-block;
    margin-top: 1.125rem;
    font-family: $headings-font-family;
    font-size: 1.25rem;
    color: $primary;

    @include media-breakpoint-up(md) {
        font-size: 1.75rem;
    }
}