.product {
    position: relative;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    @include media-breakpoint-up(lg) {
        justify-content: space-between;
    }
}

.product__thumb {
    width: 100%;
    max-width: 32.5rem;

    .swiper {
        + .swiper {
            margin-top: 0;
        }
    }

    .swiper-thumb {
        .swiper-slide {
            cursor: zoom-in;

            &.swiper-slide-zoomed {
                cursor: grab;
            }
        }
    }

    .swiper-thumbs {
        .swiper-wrapper {
            align-items: center;
        }

        .swiper-slide {
            opacity: .5;
            transition: opacity .25s ease-in-out;
            cursor: pointer;

            &:hover {
                opacity: .75;
            }

            &.swiper-slide-thumb-active {
                opacity: 1;
            }
        }
        
    }
    
}

.product__body {
    width: 100%;
    max-width: 32.1875rem;
    padding-top: 2rem;
    text-align: center;

    @include media-breakpoint-up(lg) {
        text-align: left;
    }

    .amount {
        display: flex;
        align-items: center;
        justify-content: center;

        @include media-breakpoint-up(lg) {
            justify-content: flex-start;
        }
    }

    .amount__current {
        display: inline-flex;
        align-items: center;
        height: 2.6875rem;
        margin-right: .875rem;
        padding: 0 0.75rem .125rem .75rem;
        background: #000;
        border-radius: .875rem;
        font-size: 1.5rem;
        font-weight: $fw-medium;
        color: #fff;

        @include media-breakpoint-up(md) {
            font-size: 1.5625rem;
        }
    }

    .amount__old {
       
        @include media-breakpoint-down(md) {
            font-size: 1.25rem;
        }
        @include media-breakpoint-down(sm) {
            font-size: 1rem;
        }
    }
}

.product__label {
    position: absolute;
    top: 2rem;
    left: 0;
    display: flex;
    align-items: center;
    gap: .65rem;
    z-index: 2;
}

.product__title {
    margin-top: 2.75rem;
    font-size: 1.375rem;

    @include media-breakpoint-up(sm) {
        font-size: 1.875rem;
    }
}

.product__desc {
    margin-top: 1rem;
    font-weight: $fw-semibold;
    .link {
        font-size: 0.9375rem;
        text-decoration: underline;
    }
}

.product__subtitle {
    margin-top: 1.5rem;
    font-size: 0.9375rem;
    font-weight: $fw-medium;
    line-height: 1.7;
}

.product__action {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 1.6rem;
    margin-top: 1.5rem;

    @include media-breakpoint-up(sm) {
        margin-top: 3.25rem;
    }

    @include media-breakpoint-up(lg) {
        gap: .875rem;
        justify-content: space-between;
    }
}

.product__content {
    margin-top: 2.25rem;
    text-align: left;

    @include media-breakpoint-up(sm) {
        margin-top: 3.5rem;
    }

    .toggle__button {
        padding-left: .75rem;
        padding-right: 1.5625rem;
        font-family: $headings-font-family;
        font-size: 1.25rem;

        &.active {
            color: #000;
        }
    }

    .toggle__content {
        font-weight: $fw-light;
    }

    .link {
        font-size: 0.9375rem;
        text-decoration: underline;
    }
}

.product__extra {
    margin-top: 2.125rem;
    padding-top: 1.625rem;
    border-top: 1px solid #b2b2b2;

    @include media-breakpoint-up(lg) {
        .extra-order {
            border: 0;
            margin-top: .3rem;
        }
        .extra-order__item {
            padding-top: 1rem;
            padding-bottom: 1rem;
            &:not(:first-child) {
                border: 0;
            }
        }
        .extra {
            align-items: flex-start;
            text-align: left;
        }

        .extra__body {
            width: 100%;
            padding-bottom: .75rem;
        }
        .extra__thumb {
            height: 3.5rem;
            justify-content: flex-start;

            .svg {
                width: 2.5rem;
            }
        }
        .extra__title {
            margin-top: .5rem;
            font-size: 0.9375rem;
            line-height: 1.25;
        }
        .extra__link {
            font-size: 0.8125rem;
            font-family: $headings-font-family;
            font-weight: $fw-regular;
        }
        .extra__price {
            font-size: 1.25rem;
            font-weight: $fw-semibold;
        }

        .button {
            margin-top: 1.25rem;
            padding: 0.25rem 1rem
        }
    }
}

.product__extra-heading {
    font-size: 1.375rem;
    font-family: $headings-font-family;
    font-weight: $fw-bold;

    @include media-breakpoint-up(lg) {
        font-size: 1.375rem;
    }
}