.header {
    position: fixed;
    top: 0;
    left: 0; 
    width: 100%;
    z-index: 999;

    .container {
        display: flex;
        position: relative;
    }

    &.active {
        pointer-events: none;
        
        .navbar-brand {
            opacity: 0;
            transform: scale(0.97);
        }
    }
}

.navbar-brand {
    color: #fff;
    transition: opacity .2s, transform .3s;

    &:hover,
    &:focus {
        color: #fff;
    }
}

.menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow-y: auto;
    background: rgba(#fff, .95);
    z-index: 9999;
    pointer-events: none;
    opacity: 0;
    transform: scale(1.025);
    transition: opacity .2s, transform .3s;
}

.menu-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;

    .nav-link {
        text-transform: uppercase;
        @include rfs(2rem);

        &:hover {
            color: $primary;
        }
    }
}

body {
    &.show-menu {
        .menu {
            opacity: 1;
            pointer-events: auto;
            transform: scale(1);
        }
    }
}

.button-menu {
    position: absolute;
    top: 0;
    right: .75rem;
    z-index: 9999;
}

.menu-row {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 9999;

    .container {
        position: relative;
    }
}
