.breadcrumb {
    display: none;
    margin-top: 2.25rem;
    
    @include media-breakpoint-up(md) {
        display: block;
    }
}

.breadcrumb__list {
    display: flex;
    align-items: center;
    padding: 0;
    list-style: none;
}

.breadcrumb__item {
    display: flex;
    align-items: center;
    font-weight: $fw-medium;
    font-size: 0.9375rem;

    &:not(:first-child)::before {
        content: "-";
        margin: 0 .25rem;
        flex: none;
    }
}

.breadcrumb__link {
    font-size: inherit;
    color: inherit;
    font-weight: $fw-light;
    text-decoration: none;
}
