.container {
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: 1rem;
    padding-right: 1rem;

    @include media-breakpoint-up(md) {
        max-width: 35rem;
    }

    @include media-breakpoint-up(md) {
        max-width: 46.25rem;
    }

    @include media-breakpoint-up(lg) {
        max-width: 71.6875rem;
    }
}
