.box {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100%;
    padding: 2rem 1rem;
    margin-right: 1px;
    border: 1px solid $light;
    text-align: center;

    @include media-breakpoint-up(md) {
        margin: 0;
    }
}

.box__icon {
    width: 4.5rem;

    @include media-breakpoint-up(sm) {
        width: 5.5rem;
    }
}

.box__title {
    margin-top: 2rem;
    font-size: .75rem;
    font-weight: $fw-semibold;

    @include media-breakpoint-up(sm) {
        font-size: 1.25rem;
    }
}
