.cart__list {
    margin-top: 1.25rem;

    @include media-breakpoint-up(lg) {
        margin-top: 1.875rem;
        border: 1px solid $light;
    }

    .card-row {
        border-bottom: 1px solid $light;

        &:last-child {
            @include media-breakpoint-up(lg) {
                border: none;
            }
        }
    }

    .amount {
        margin-top: .5rem;
    }
}

.cart__discount {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 3rem;
    
    .heading {
        width: 100%;
        font-size: 1.375rem;

        @include media-breakpoint-up(md) {
            font-size: 1.5625rem;
        }

        @include media-breakpoint-up(lg) {
            width: 26rem;
            margin: 0;
        }
    }

    .discount {
        margin-top: 1rem;

        @include media-breakpoint-up(lg) {
            width: calc(100% - 26rem);
            margin-top: 0;
        }
    }
   
}

.cart__action-row {
    display: flex;
    flex-wrap: wrap;
    margin-top: 2.25rem;
    border-top: 1px solid #000;
}

.cart__action-col {
    width: 100%;
    padding: 2.25rem 1rem;

    @include media-breakpoint-up(md) {
        padding: 4.25rem 3.75rem;
    }

    @include media-breakpoint-up(lg) {
        max-width: 34.375rem;
    }
    
    &:last-child {
        border: 1px solid #000;
        margin-left: auto;

        @include media-breakpoint-up(lg) {
            border-top: 0;
        } 

        @include media-breakpoint-down(sm) {
            .button {
                height: auto;
                padding: 0.5rem 1.25rem;
            }
        }
    }

    .button {
        @include media-breakpoint-up(md) {
            font-size: 1rem;
        }
    }
}

.cart__action-icons {
    display: flex;
    gap: 4.25rem;
}

.cart__payment,
.cart__delivery {
    .form-check-label {
    
        @include media-breakpoint-up(md) {
            font-size: 1.25rem;
        }

        &:before,
        &:after {
            @include media-breakpoint-up(md) {
                top: -0.0625rem;
            }
        }
    }
}

.cart__contact {
    max-width: 50.75rem;

    .form-check {
        max-width: 30.625rem;
    }
}

.cart__address {
    max-width: 50.75rem;
}

.cart__action-desc {
    font-size: .875rem;

    a {
        color: inherit;
        text-decoration: underline;

        &:hover,
        &:focus {
            color: $primary;
        }
    }
 
}

.table-summary {
    margin-left: auto;
    font-size: 1.25rem;

    th {
        font-weight: 400;
    }
}

.table-summary-amount {
    font-family: $font-family-condensed;
    font-weight: $fw-semibold;
    font-size: 1.4375rem;
}

.summary {
    display: inline-block;
    padding-left: 1.75rem;
    border-top: 1px solid #ccc;
    font-weight: bold;
    font-size: 1.25rem;
}

.summary-amount {
    font-family: $font-family-condensed;
    font-size: 1.875rem;
    font-weight: $fw-semibold;
}