.font-base {
    font-family: $font-family-base;
}

.fw-medium {
    font-weight: 500;
}

.fw-600 {
    font-weight: 600;
}

.img-fit {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.img-fluid {
    max-width: 100%;
    height: auto;
}

.svg {
    max-width: 100%;
    height: auto;
    fill: currentColor;
}

a {
    &:focus {
        outline: none;
        color: $primary;
    }
}

button {
    &:focus {
        outline: none;
    }
}

.text-primary {
    color: $primary;
}

.btn-close {
    background: none;
    border: 0;
}

.rounded-top-0 {
    border-top-right-radius: 0!important;
    border-top-left-radius: 0!important;
}