.amount {
    font-family: $font-family-condensed;
    font-size: 0;
}

.amount__current {
    display: inline-block;
    margin-right: .5rem;
    font-weight: $fw-semibold;
    font-size: 1rem;

    @include media-breakpoint-up(sm) {
        font-size: 1.125rem;
    }

    @include media-breakpoint-up(md) {
        margin-right: .875rem;
        font-size: 1.4375rem;
    }
}

.amount__old {
    display: inline-block;
    position: relative;
    font-size: .75rem;
    font-weight: $fw-regular;

    @include media-breakpoint-up(md) {
        font-size: 1.25rem;
    }

    &:before {
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        width: 91%;
        height: 1px;
        background: #000;
        transform: rotate(-45deg);
    }
}

.amount__label {
    display: inline-flex;
    align-items: center;
    height: 2.625rem;
    margin-right: -2.3rem;
    padding: 0 3rem .125rem 1.25rem;
    border-radius: .875rem 0 0 .875rem;
    background: #f2f2f2;
    text-transform: uppercase;
    font-weight: $fw-medium;
    font-size: 1.0625rem;
}