.icon-text {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 1.875rem;

    @include media-breakpoint-up(lg) {
        flex-direction: row;
    }
}

.icon-text__body {
    font-size: .875rem;
    line-height: 1.2;
    text-align: center;
    font-weight: $fw-regular;

    strong {
        font-weight: $fw-bold;
    }

    @include media-breakpoint-up(md) {
        font-size: 1.25rem;
    }

    @include media-breakpoint-up(lg) {
        order: -1;
        text-align: right;
    }
}

