.heading {
    font-family: $headings-font-family;
    font-size: 1.75rem;
    font-weight: $fw-bold;

    @include media-breakpoint-up(sm) {
        font-size: 1.85rem;
    }

    @include media-breakpoint-up(md) {
        font-size: 2.125rem;
    }
}

.heading_top {
   
    @include media-breakpoint-up(md) {
        padding: 0 1.125rem;
    }
}

.heading-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: .75rem;
}