.card {
    display: block;
    color: #000;
    text-decoration: none;

    &:hover,
    &:focus {
        color: $primary;
    }

    .amount {
        margin-top: .65rem;
    }

    @include media-breakpoint-down(md) {
        .heading_lg {
            font-size: 1.125rem;
        }
        .heading_md {
            font-size: 1rem;
        }
    }

    @include media-breakpoint-down(sm) {
        .heading_lg {
            font-size: 1rem;
        }
        .heading_md {
            font-size: .75rem;
        }
        .amount {
            gap: .5rem;
        }
    }
    
}

.card__thumb {
    position: relative;

    .label {
        position: absolute;
        top: 1.5rem;
        left: 0;

        @include media-breakpoint-up(md) {
            top: 2.5rem;
            left: 1.125rem;
        }
    }
}

.card__body {
    @include media-breakpoint-up(md) {
        padding: 0 1.125rem;
    }
}

.card__title {
    font-size: .75rem;
    line-height: 1.5;
    font-weight: $fw-semibold;
    text-decoration: none;
    color: inherit;

    &:focus,
    &:hover {
        color: $primary;
    }

    @include media-breakpoint-up(sm) {
        font-size: 1rem;
    }

    @include media-breakpoint-up(md) {
        font-size: 1.0625rem;
    }
}
