.extra {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    text-align: center;

    .button {
        margin-top: 1rem;

        @include media-breakpoint-up(sm) {
            margin-top: 1.875rem;
        }
    }
}

.extra__thumb {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 2.5rem;

    @include media-breakpoint-up(sm) {
        height: 4.0625rem;
    }

    .svg {
        max-height: 100%;
    }
}

.extra__body {
    padding-bottom: .625rem;
}

.extra__title {
    margin-top: 1.125rem;
    margin-bottom: 0;
    font-family: $headings-font-family;
    font-size: 1rem;

    @include media-breakpoint-up(sm) {
        margin-bottom: .375rem;
        font-size: 1.25rem;
    }
}

.extra__link {
    color: inherit;
    font-weight: $fw-bold;
    font-size: .875rem;

    @include media-breakpoint-up(sm) {
        font-size: 1rem;
    }
}

.extra__price {
    font-family: $font-family-condensed;
    font-size: 1.5rem;
    font-weight: $fw-bold;

    @include media-breakpoint-up(sm) {
        font-size: 1.875rem;
    }
}
