.newsletter {
    padding: 1rem 2rem;
    border: 1px solid #e8e9ed;
    border-radius: .625rem;

    .form-control {
        background: #f2f2f2;
        border: 0;
        border-radius: .625rem .625rem 0 0;
    }
}

.newsletter-title {
    font-family: $headings-font-family;
    font-size: 1.125rem;
    font-weight: $fw-bold;
}