.social {
    display: inline-flex;
    align-items: center;
    list-style: none;
    padding: 0;
    gap: 1.125rem;
}

.social__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 3rem;
    height: 3rem;
    background: none;
    border: 1px solid $light;
    border-radius: 50%;
    color: #000;
}
