.extra-order {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-top: 1.5rem;
    margin-left: -1rem;
    margin-right: -1rem;
    border: 1px solid $light;

    @include media-breakpoint-up(md) {
        margin-top: 3.125rem;
        margin-left: 0;
        margin-right: 0;
    }

    @include media-breakpoint-up(lg) {
        flex-wrap: nowrap
    }
}

.extra-order__item {
    width: 50%;
    padding: 1.5rem 1rem 2rem;

    @include media-breakpoint-up(lg) {
        padding: 3.5rem 1rem 3.625rem;
    }

    @include media-breakpoint-down(lg) {
        width: 50%;
        border-left: 1px solid $light;

        &:nth-child(2n+1) {
            border-left: none;
        }

        &:nth-child(1n+3) {
            border-top: 1px solid $light;
        }
    }

    @include media-breakpoint-up(lg) {
        width: 100%;
        border: none;

        &:not(:first-child) {
            border-left: 1px solid $light;
        }
    }
    
}

