.toggle__button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1.5;
    width: 100%;
    padding: 1.125rem 0;
    font-size: 1.0625rem;
    background: transparent;
    border: none;
    border-bottom: 1px solid #e5e5e5;
    color: #000;
    text-align: left;

    &.active {
        color: $primary;
        
        .svg {
            transform: rotate(180deg);
        }

        + .toggle__content {
            display: block;
        }
    }

    .svg {
        flex: none;
        transition: transform .3s;
        pointer-events: none;
    }
}

.toggle__content {
    display: none;
    padding: 1.25rem 0;
}
