.discount__field {
    position: relative;
    width: 100%;

    .feedback {
        @include media-breakpoint-up(lg) {
            position: absolute;
            top: 100%;
            right: 0;
        }
    }
}

.discount__input {
    width: 100%;
    height: 3.125rem;
    padding: 0 1.25rem;
    border: none;
    border-radius: 0.625rem;
    background: #f2f2f2;
    font-size: 1.0625rem;
    outline: 0;
}

.link.active {
    + .discount {
        display: flex;
    }
}

.discount {
    display: none;
    width: 100%;
    max-width: 24rem;
    flex-direction: column;
    align-items: flex-start;
    margin-left: auto;

    @include media-breakpoint-up(lg) {
        max-width: 100%;
        flex-direction: row;
        align-items: center;
        gap: 2.125rem;
    }
}