$body-bg: #ffffff;
$body-color: #000;
$light: #e5e5e5;
$text-muted: #323232;
$dark: #525252;

$line-height-base: 1.4;

$primary: #fc5453;
$secondary: #000;

$fw-light: 300;
$fw-regular: 400;
$fw-medium: 500;
$fw-semibold: 600;
$fw-bold: 700;
$fw-extrabold: 800;

$enable-negative-margins: true;
$enable-smooth-scroll: false;

$font-family-base: 'Nunito', sans-serif;
$font-family-condensed: 'Barlow Condensed', sans-serif;

$headings-font-weight: $fw-bold;
$headings-line-height: 1.3;
$headings-font-family: 'Adelle', sans-serif;

$link-hover-color: #fc5453;

$h1-font-size: 2.125rem; //34px
$h2-font-size: 1.875rem; //30px
$h3-font-size: 1.5625rem; //25px
$h4-font-size: 1.25rem; //20px
$h5-font-size: 1.0625rem; //17px
$h6-font-size: .9375rem; //15px

$grid-gutter-width: 1.125rem;

$grid-breakpoints: (
    xs: 0,
    sm: 460px,
    md: 768px,
    lg: 1280px,
    xl: 1600px,
) !default;
