.footer-head {
    @extend .px-4, .px-lg-0;
    max-width: 100%;
    padding-bottom: .875rem;
    border-bottom: 1px solid #e5e5e5;
    font-size: 1.125rem!important;
    font-weight: 400;
    color: #000;
    text-decoration: none;
    text-align: left;

    @include media-breakpoint-up(md) {
        padding-bottom: 1.25rem;
    }
}

a.footer-head {
    @include media-breakpoint-down(md) {
        padding: 0!important;
        font-size: .875rem!important;
    }
}

.footer-nav {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1rem;
    text-align: left;
    list-style: none;

    li {
        @extend .px-4, .px-lg-0;
        width: calc(50% - .5rem);
    }

    @include media-breakpoint-up(lg) {
        max-width: 100%;
        flex-direction: column;

        li {
            width: 100%;
        }
    }
}

.footer-nav-link {
    font-size: .875rem;
    font-weight: 300;
    line-height: 1.2;
    text-decoration: none;
    color: #000;
    white-space: nowrap;

    @include media-breakpoint-up(md) {
        font-size: 1.125rem;
    }
}

.footer-social {
    @extend .px-4, .px-lg-0;
    display: flex;
    gap: 1.125rem;
    align-items: center;
    justify-content: flex-start;
    list-style: none;

    @include media-breakpoint-up(lg) {
        justify-content: flex-end;
    }
}

.footer-social-link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    border: 1px solid #e5e5e5;
    color: #000;
    font-size: 1.25rem;
}

.footer-logos {
    margin-top: 1.5rem;
    padding: 1.5rem 0;
    border-top: 1px solid #e5e5e5;

    @include media-breakpoint-up(md) {
        padding: 3.375rem 0;
    }
}
