.nav-overlay {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    background: rgba(#fff, 1);
    padding: 2rem 1rem 1rem;
    z-index: 999;
    opacity: 0;
    pointer-events: none;
    overflow-y: auto;
    transform: scale(1.025);
    transition: opacity .2s,transform .3s;

    @include media-breakpoint-up(lg) {
        padding: 0;
    }
    
    &.active {
        pointer-events: auto;
        opacity: 1;
        transform: scale(1);
    }
    
    @include media-breakpoint-up(lg) {
        position: static;
        width: auto;
        height: auto;
        background: none;
        opacity: 1;
        pointer-events: auto;
        transform: scale(1);
    }
}