.button {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap: 0.8125rem;
    height: 2.5rem;
    padding: .25rem 1.5rem;
    background: #000;
    border: 2px solid #000;
    border-radius: 0.625rem;
    font-weight: $fw-bold;
    text-transform: uppercase;
    font-size: .75rem;
    color: #fff;
    text-decoration: none;
    text-align: center;
    line-height: 1.2;
    box-shadow: 0 0.5625rem 1rem rgba(#d1d1d1, .56);
    transition: box-shadow .25s ease-in-out;

    &:hover {
        background: #fff;
        color: #000;
    }

    &:focus {
        background: #000;
        color: #fff;
        box-shadow: 0 0 0 0.25rem rgba(0,0,0,.25);
    }

    &.disabled,
    &:disabled {
        pointer-events: none;
        opacity: .5;
    }

    .svg-cart-2 {
        width: 1.25rem;
    }
}


.button_primary {
    background: $primary;
    border-color: $primary;
    box-shadow: 0 0.5625rem 1rem rgba($primary, .56);
    color: #fff;

    &:hover {
        background: #fff;
        color: $primary;
    }

    &:focus {
        background: $primary;
        color: #fff;
        box-shadow: 0 0 0 0.25rem rgba($primary,.25);
    }
}

.button_lg {
    height: 3.875rem;
    border-radius: .75rem;
    font-size: 0.9375rem;
}
