.quantity {
    display: flex;
    align-items: center;
    gap: 1rem;
    font-size: 1.25rem;
    font-weight: $fw-bold;
}

.quantity__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2.25rem;
    height: 2.25rem;
    padding-bottom: .125rem;
    border: none;
    background: #f2f2f2;
    border-radius: 50%;
    font-size: 1.375rem;
    font-weight: $fw-extrabold;
    color: #000;

    &:hover,
    &:focus {
        color: $primary;
    }
}

.quantity__field::-webkit-outer-spin-button,
.quantity__field::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.quantity__field {
    width: 2.6875rem;
    height: 2.6875rem;
    background: $primary;
    border-radius: 50%;
    border: none;
    text-align: center;
    -moz-appearance: textfield;
    color: #fff;
    overflow: hidden;
    font-weight: $fw-bold;
    font-size: 1.0625rem;

    &:focus {
        outline: 0;
    }
}
