.footer {
    margin: 3rem 0 0;

    @include media-breakpoint-up(md) {
        margin-top: 8rem;
    }
}

.footer__row {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;

    @include media-breakpoint-up(lg) {
        flex-direction: row;
        gap: 0;
    }

    .social {
        padding: 0 .75rem;

        @include media-breakpoint-up(sm) {
            padding: 0 1.5rem;
        }

        @include media-breakpoint-up(lg) {
            padding: 0;
        }
    }
}

.footer__row_copy {
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding-bottom: 1rem;
    margin-top: 1.25rem;
    text-align: center;
    flex-wrap: wrap;

    .footer__col_big {
        width: 100%;

        @include media-breakpoint-up(lg) {
            width: auto;
        }
    }
    
    .footer__col {
        @include media-breakpoint-down(lg) {
            width: auto;
        }
    }

    @include media-breakpoint-up(lg) {
        margin-top: 2rem;
        text-align: left;
    }

    .link {
        font-size: .75rem;

        @include media-breakpoint-up(sm) {
            font-size: .875rem;
        }

        @include media-breakpoint-up(md) {
            font-size: 1.0625rem;
        }
    }
}

.footer__row_logos {
    padding: 2rem 0;
    justify-content: center;
    border-top: 1px solid $light;

    @include media-breakpoint-up(lg) {
        padding: 3.75rem 0;
        justify-content: flex-end;
    }
    .footer__col_big {
        @include media-breakpoint-up(lg) {
            margin-left: 2.5rem;
        }
    }
}

.footer__col {
    width: 100%;

    @include media-breakpoint-up(lg) {
        width: 20%;
    }
}

.footer__col_big {
    width: 100%;

    @include media-breakpoint-up(lg) {
        width: 40%;
        text-align: right;
        margin-left: auto;
    }
}

.footer__heading {
    padding: .875rem .75rem;
    margin-bottom: 1rem;
    font-size: 1.0625rem;
    font-weight: $fw-semibold;
    letter-spacing: .5px;
    border-bottom: 1px solid $light;

    @include media-breakpoint-up(sm) {
        padding-left: 1.5rem;
        padding-right: 1.5rem;
    }

    @include media-breakpoint-up(lg) {
        padding: 1.25rem 0;
        margin-bottom: 1.5rem;
    }
}

.footer__nav {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    list-style: none;
    padding: 0;
    gap: .875rem;
    margin: 0;
    font-size: 0.75rem;

    @include media-breakpoint-up(sm) {
        font-size: 0.875rem;
    }

    @include media-breakpoint-up(md) {
        font-size: 0.9375rem;
    }

    .link {
        font-weight: $fw-regular;
        font-size: inherit;
    }
}

.footer__nav-item {
    width: calc(50% - 0.45rem);
    padding: 0 .75rem;

    @include media-breakpoint-up(sm) {
        padding: 0 1.5rem;
    }

    @include media-breakpoint-up(lg) {
        width: 100%;
        padding: 0;
    }
}

.foot__copy {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
    font-size: .875rem;

    @include media-breakpoint-up(lg) {
        flex-direction: row;
        margin-left: auto;
        gap: 1.75rem;
        width: auto;
        font-size: 1rem;
    }
    
    .logo {
        @include media-breakpoint-up(lg) {
            order: 1;
        }
    }
}
